import { Injectable } from '@angular/core';
import { User } from '../../class/user';
import { MenuTab } from '../../class/menu-tab';
import { SmsSegmented } from '../../class/sms-segmented';

@Injectable()
export class SettingService {
  private tabs: MenuTab[];
  private userMenus: MenuTab[];
  private smsSegmenteds: SmsSegmented[];
  private navTitle: String;
  private title: String;
  private currentUser: User;

  constructor() {
    this.title = this.navTitle = '1.0';

    this.tabs = [
      {title: '', icon: 'sms', url: '/home/sms'},
      {title: '', icon: 'work', url: '/home/work'},
      {title: '', icon: 'user', url: '/home/user'}
    ];

    this.userMenus = [
      {title: '', icon: 'info', url: '/user/info', color: '#0099CC'},
      {title: '', icon: 'setting', url: '/user/setting', color: '#0099CC'},
      {title: '', icon: 'about', url: '/user/about', color: '#0099CC'}
    ];

    this.smsSegmenteds = [
      {name: '', badge: 4, url: '/home/sms/upcoming'},
      {name: '', badge: 6, url: '/home/sms/notice'}
    ];

    this.currentUser = {
      id: 0,
      name: ' ',
      isAvatar: false,
      avatar: 'avatar'
    };
  }

  /**
   * 
   */
  getTitle(): String {
    return this.navTitle;
  }

  /**
   * 
   */
  isTitle(): Boolean {
    return this.navTitle === this.title;
  }

  /**
   * 
   * @param title ,
   */
  setTitle( title: String = this.title) {
    // if (dd.version !== null) {
    //   dd.biz.navigation.setTitle({
    //     title : title,
    //     onSuccess : function(result) {
    //       console.error(result);
    //     },
    //     onFail : function(err) {
    //       console.error(err);
    //     }
    //   });
    // }
    this.navTitle = title;
  }

  /**
   * 
   */
  getTabBarNames(): MenuTab[] {
    return this.tabs;
  }

  /**
   * 
   */
  getUserMenus(): MenuTab[] {
    return this.userMenus;
  }

  /**
   * 
   */
  getSmsSegmenteds(): SmsSegmented[] {
    return this.smsSegmenteds;
  }

  /**
   * 
   */
  getUser(): User {
    return this.currentUser;
  }

}



